'use strict';

// MODULES //

import numelDimension from '@stdlib/ndarray-base-numel-dimension';
import getStride from '@stdlib/ndarray-base-stride';
import getOffset from '@stdlib/ndarray-base-offset';
import getData from '@stdlib/ndarray-base-data-buffer';
import { ndarray as strided } from '@stdlib/stats-strided-mskmaxabs';


// MAIN //

/**
* Computes the maximum absolute value of a one-dimensional ndarray according to a mask.
*
* @param {ArrayLikeObject<Object>} arrays - array-like object containing an input ndarray and a mask ndarray
* @returns {number} maximum absolute value
*
* @example
* import ndarray from '@stdlib/ndarray-base-ctor';
*
* var xbuf = [ 1.0, -5.0, 4.0, 2.0 ];
* var x = new ndarray( 'generic', xbuf, [ 4 ], [ 1 ], 0, 'row-major' );
*
* var maskbuf = [ 0, 0, 1, 0 ];
* var mask = new ndarray( 'generic', maskbuf, [ 4 ], [ 1 ], 0, 'row-major' );
*
* var v = mskmaxabs( [ x, mask ] );
* // returns 5.0
*/
function mskmaxabs( arrays ) {
	var mask = arrays[ 1 ];
	var x = arrays[ 0 ];
	return strided( numelDimension( x, 0 ), getData( x ), getStride( x, 0 ), getOffset( x ), getData( mask ), getStride( mask, 0 ), getOffset( mask ) ); // eslint-disable-line max-len
}


// EXPORTS //

export default mskmaxabs;
